import Vue from 'vue'
import axios from 'axios'

/**
 * 200
	207
	208id
	206
	204
	10003
 */

export function cutoutByJpg(path, type = 1) { 
	// axios
	let instance = axios.create({timeout: 5000});

    return new Promise((resolve, reject) => {         
        instance.post('http://ht.idealead.hbindex.com/webapi/User_cutout/upload_file_once', {
        	path : path,
            type: type,
        	user_id: Vue.prototype.getCookie('author_id')
        })        
        .then(res => { 
        	// console.log(res.data)
        	resolve(res.data);
        	
        })        
        .catch(err => {            
            reject(err)        
        })    
    });
}